.container-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    gap: 24px 24px;
    grid-auto-flow: row;
    grid-template-areas:
        "CV CV portfolio portfolio portfolio portfolio . . feedback feedback education education"
        "CV CV portfolio portfolio portfolio portfolio . . contacts contacts contacts contacts"
        "tools tools skills skills discuss discuss discuss discuss contacts contacts contacts contacts"
        "tools tools skills skills discuss discuss discuss discuss Services Services Services Services";
}

.CV {
    grid-area: CV;
    background: #020e21;
    min-height: 100px;
}

.portfolio {
    grid-area: portfolio;
    background: #020e21;
    min-height: 100px;
}

.feedback {
    grid-area: feedback;
    background: #020e21;
    min-height: 100px;
}

.education {
    grid-area: education;
    background: #020e21;
    min-height: 100px;
}

.contacts {
    grid-area: contacts;
    background: #020e21;
    min-height: 100px;
}

.tools {
    grid-area: tools;
    background: #020e21;
    min-height: 100px;
}

.skills {
    grid-area: skills;
    background: #020e21;
    min-height: 100px;
}

.discuss {
    grid-area: discuss;
    background: #020e21;
    min-height: 100px;
}

.Services {
    grid-area: Services;
    background: #020e21;
    min-height: 100px;
}