footer {
    padding: 120px 0;
    margin-top: auto;
    background: $bg-footer;
    border-radius: 20px 20px 0 0;

    .row {
        display: flex;
        justify-content: space-between;

        &:not(:last-child) {
            border-bottom: 2px solid #448bc333;
            padding-bottom: 40px;
            margin-bottom: 40px;
            align-items: center;
        }

        &:last-child {
            flex-wrap: wrap;
            row-gap: 20px;
        }
    }

    h3,
    .h3 {
        color: #ff8198;
    }

    .title {
        color: #85939e;
        margin-bottom: 24px;
    }

    .footer-social {
        display: flex;
        flex-direction: column;
        gap: 32px;
        max-width: 515px;

        .logo {
            max-width: 60px;
            width: 100%;
        }

        span {
            font-size: 20px;
            color: #99a7b2;
            line-height: 1.4;
        }

        .social-icon {
            transform: rotate(0);
        }
    }

    .menu-footer {
        display: flex;
        max-width: 408px;
    }

    .menu {
        margin-right: 57px;
    }

    .list {
        font-size: 14px;
        display: flex;
        flex-direction: column;
        gap: 16px;
        overflow: hidden;
        padding-bottom: 1px;

        &-link {
            color: #5d6b76;
            position: relative;


            &::after {
                content: '';
                display: block;
                position: absolute;
                width: 105%;
                height: 1px;
                background-color: #266da5;
                left: -110%;
                transition: .2s all ease-out;
            }

            &:hover::after {
                left: 0;
            }
        }
    }

    .more-info {
        max-width: 300px;

        .title {
            color: #99a7b2;
            margin-bottom: 16px;
        }
    }

    .input-box {
        position: relative;
    }

    input {
        border: 1px solid #266da5;
        border-radius: 8px;
        padding: 20.5px 12px;
        color: #a5c1da;
        width: 100%;

        &::placeholder {
            font-size: 16px;
            text-transform: capitalize;
            color: inherit;
        }
    }

    .icon-arrow-right {
        fill: #266da5;
        stroke-width: 1px;
        stroke: #266da5;
        width: 20px;
        height: 20px;
        display: inline-block;
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);

        svg {
            height: 100%;
        }
    }

}

@media screen and (max-width: 1280px) {

    footer {
        .footer-social {
            max-width: 202px;

            span {
                font-size: 16px;
                line-height: 1.2;
            }
        }
    }
}

@media screen and (max-width: 660px) {
    footer {
        padding: 40px 0;

        .row:last-child {
            flex-direction: column;
        }

        h3,
        .h3 {
            font-size: 16px;
        }

        .footer-social,
        .menu-footer {
            max-width: 100%;
        }

        .menu-footer {
            justify-content: space-between;
        }
    }
}

@media screen and (max-width: 480px) {
    footer {
        .menu-footer {
            flex-direction: column;
            gap: 40px;
        }

        .menu {
            margin-right: initial;
        }
    }
}

@media screen and (max-width: 360px) {
    footer {
        .title {
            font-size: 16px;
        }
    }
}