.first-screen {
    .container {
        position: relative;
    }

    .title-box {
        position: absolute;
        max-width: 948px;
        width: 100%;
        top: -100px;
        right: 20px;
    }

    h1,
    .h1 {
        color: #62a9e1;
        margin-bottom: 32px;
    }

    h2,
    .h2 {
        font-size: 56px;
        color: #ff8198;
        justify-content: end;
    }

    h1,
    .h1,
    h2,
    .h2 {
        display: flex;

        span {
            transition: .4s all ease-in-out;

            &:hover {
                margin-top: -20px;
            }
        }
    }

    .row {
        display: flex;
        justify-content: space-between;
        align-items: end;
    }

    .column {
        flex-basis: 624px;
    }

    .user-photo {
        max-width: 624px;
        width: 100%;
        position: relative;
        max-height: 571px;
        height: 100%;

        img {
            mask-image: url('assets/img/photo.png');
            mask-repeat: no-repeat;
            width: 100%;
        }
    }

    p {
        font-size: 20px;
        line-height: 1.4;
        color: #99a7b2;
        margin-bottom: 35px;
        max-width: 515px;
    }

}

@media screen and (max-width: 1310px) {
    .first-screen {
        .column:last-child {
            padding-left: 10px;
        }
    }
}

@media screen and (max-width: 1280px) {
    .first-screen {
        .title-box {
            position: relative;
            inset: 0;
            max-width: 960px;
            margin: 0 auto 30px;
        }

        .column:first-child {
            align-self: flex-start;
        }

        // .user-photo {
        //     max-width: 470px;
        //     max-height: 430px;
        // }
    }
}

@media screen and (max-width: 964px) {
    .first-screen {
        .row {
            flex-direction: column;
            align-items: start;
            gap: 20px;
        }

        .column {
            flex-basis: initial;

            &:last-child {
                padding-left: 0;
            }

            &:first-child {
                align-self: center;
            }
        }

        .title-box {
            max-width: 100%;
        }

        h1,
        .h1 {
            font-size: 60px;
            margin-bottom: 24px;
        }

        h2,
        .h2 {
            font-size: 42px;
        }

        .user-photo {
            margin: 0 auto;
        }

        p {
            max-width: 100%;

            &:last-of-type {
                margin-bottom: 20px;
            }
        }
    }
}

@media screen and (max-width: 660px) {
    .first-screen {

        h1,
        .h1 {
            font-size: 36px;
        }

        h2,
        .h2 {
            font-size: 25px;
        }
    }
}

@media screen and (max-width: 479px) {
    .first-screen {

        h1,
        .h1 {
            font-size: 26px;
            margin-bottom: 12px;
        }

        h2,
        .h2 {
            font-size: 18px;
        }
    }
}