html,
body {
    font-family: $font-primary, $font-secondary;
    font-size: $font-size;
    line-height: 1.2;
}

.bg-wrap {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    // backdrop-filter: blur(50px);
    box-shadow: inset 0 0 60px 0 rgba(255, 255, 255, 0.05), inset 0 4px 4px 0 rgba(255, 255, 255, 0.15);
    background: url(../assets/img/background.png), hsla(240, 100%, 3%, 0.8); // Спитати за бг колір!!!
    background-repeat: no-repeat;
    background-size: cover;
}


.container {
    max-width: calc(1272px + ($padding * 2));
    width: 100%;
    padding: 0 $padding;
    margin: 0 auto;
}

// @media screen and (max-width: 1300px) {
//     .container {
//         max-width: 1000px;
//     }
// }

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    text-transform: uppercase;
    line-height: 1;
    font-weight: 700;
}

h1,
.h1 {
    font-size: 80px;
}

h2,
.h2 {
    font-size: 60px;
}

h3,
.h3 {
    font-size: 32px;
}

h4,
.h4 {
    font-size: 24px;
}

h5,
.h5 {
    font-size: 20px;
}

h6,
.h6 {
    font-size: 16px;
}

p {
    a {
        color: #266da5;

        &:hover {
            text-decoration: underline;
        }
    }
}

.m-r-m {
    margin-right: 50px;
}

@media screen and (max-width: 964px) {
    .m-r-m {
        margin-right: 37px;
    }
}

@media screen and (max-width: 660px) {
    .m-r-m {
        margin-right: 22px;
    }
}

@media screen and (max-width: 479px) {
    .m-r-m {
        margin-right: 16px;
    }
}

.m-r-s {
    margin-right: 17px;
}

@media screen and (max-width: 964px) {
    .m-r-s {
        margin-right: 12px;
    }
}

@media screen and (max-width: 660px) {
    .m-r-s {
        margin-right: 7px;
    }
}

@media screen and (max-width: 479px) {
    .m-r-s {
        margin-right: 5px;
    }
}

section {
    margin-bottom: 120px;
}