@font-face {
    font-family: 'Roboto';
    src: url('./assets/fonts/Roboto-Light.woff') format('woff'),
        url('./assets/fonts/Roboto-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

