.btn-contact {
    padding: 17px 72px;
    border-radius: 16px;
    border: 2px solid #266da5;
    background-color: #266da5;
    font-size: 20px;
    font-weight: 700;
    color: #333;
    text-transform: uppercase;
    position: relative;
    overflow: hidden;
    transition: all .45s ease-Out;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;

    &::after {
        content: '';
        display: block;
        width: 50px;
        height: 50px;
        line-height: 31px;
        border-radius: 50%;
        background-color: #62a9e1;
        position: absolute;
        // top: 31px;
        // left: 119px;
        opacity: 0;
        transition: all .5s ease-Out;
        z-index: -1;
    }

    &:hover::after {
        width: 500px;
        height: 500px;
        // top: -119px;
        // left: -31px;
        opacity: 1;

    }

    &:active {
        color: #a5c1da;
        border-color: #003b73;
    }

    &:active::after {
        background-color: #003b73;
    }
}

@media screen and (max-width: 660px) {
    .btn-contact {
        padding: 12.5px 43.5px;
        font-size: 16px;
    }
}

@media screen and (max-width: 360px) {
    .btn-contact {
        padding: 12px 28px;
        font-size: 14px;

        &::after {
            width: 10px;
            height: 10px;
        }
    }
}

.btn-about {
    padding: 54.5px 16.5px;
    box-shadow: 1px 2px 6px 0 rgba(49, 50, 51, 0.1), 5px 10px 11px 0 rgba(49, 50, 51, 0.09), 10px 22px 15px 0 rgba(49, 50, 51, 0.05), 18px 40px 17px 0 rgba(49, 50, 51, 0.01), 28px 62px 19px 0 rgba(49, 50, 51, 0);
    background: #266da5;
    border: 4px solid #266da5;
    border-radius: 50%;
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #333;
    transition: .3s all ease;
    display: flex;
    gap: 6px;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        background-color: #62a9e1;
        border-radius: 50%;
        z-index: -1;
        transition: .3s all ease;
    }

    &:hover:after {
        width: 98%;
        height: 98%;
    }
}

@media screen and (max-width: 1280px) {
    .btn-about {
        padding: 38.5px 17px;

        span {
            display: none;
        }
    }
}

@media screen and (max-width: 964px) {
    .btn-about {
        padding: 54.5px 33px;
    }
}

@media screen and (max-width: 660px) {
    .btn-about {
        padding: 38.5px 17px;
    }
}

@media screen and (max-width: 500px) {
    .btn-about {
        padding: 30px 13px;
        font-size: 16px;
    }
}

@media screen and (max-width: 400px) {
    .btn-about {
        padding: 22px 7px;
        font-size: 14px;
    }

}


.btn-order {
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    color: #333;
    box-shadow: 3px 3px 9px 0 rgba(49, 50, 51, 0.15), 11px 12px 16px 0 rgba(49, 50, 51, 0.13), 25px 26px 21px 0 rgba(49, 50, 51, 0.08), 44px 46px 25px 0 rgba(49, 50, 51, 0.02), 69px 72px 28px 0 rgba(49, 50, 51, 0);
    background: #e33b52;
    border: 3px solid #e33b52;
    display: block;
    width: 100%;
    padding-top: 28px;
    padding-bottom: 29px;
    border-radius: 16px;
    margin-top: 99px;
    transition: .3s all ease;

    &:hover {
        color: #e33b52;
        background-color: transparent;
    }
}

@media screen and (max-width: 1000px) {
    .btn-order {
        margin-top: 84px;
        padding-top: 12px;
        padding-bottom: 13px;
        font-size: 16px;
    }
}

@media screen and (max-width: 964px) {
    .btn-order {
        width: initial;
        padding: 28px 186px 29px;
        margin: 0 auto;
    }
}

@media screen and (max-width: 660px) {
    .btn-order {
        padding: 12px 0 13px;
        width: 100%;
        font-size: 16px;
    }

}