header {
	padding: 40px 0 45px 0;

	.container {
		position: relative;
	}

	.logo {
		width: 83px;
		min-height: 84px;
		align-self: flex-start;
	}
}

.header-box {
	display: flex;
	justify-content: space-between;
}

.social-box {
	display: flex;
	align-items: center;
	gap: 16px;
	transform: rotate(90deg);
	position: absolute;
	right: -150px;
	bottom: -330px;
	z-index: 1;

	span {
		font-weight: 300;
		font-size: 20px;
		text-transform: uppercase;
		color: #266da5;
		display: flex;
		align-items: center;
		gap: 16px;
		cursor: default;

		&::after {
			content: '';
			display: inline-block;
			width: 120px;
			height: 1px;
			background-color: rgba(38, 109, 165, 0.6);
		}
	}
}

.social-icon {
	display: inline-block;
	width: 44px;
	height: 44px;
	padding: 8px;
	border: 1px solid transparent;
	border-radius: 50px;
	transition: .3s all linear;
	transform: rotate(-90deg);

	svg {
		height: 100%;
		fill: #266da5;
	}

	&:hover {
		border-color: #62a9e1;

		svg {
			fill: #62a9e1;
		}
	}
}

.icon-tg {
	padding: 9px 13.3px 9.3px 5px;
}

.languages-box {
	display: flex;
	align-self: flex-start;
	gap: 8px;

	button {
		text-transform: uppercase;
		font-weight: 700;
		color: rgba(38, 109, 165, 0.4);
		font-size: 24px;
	}

	.lang-active {
		color: #266da5;
	}
}

@media screen and (max-width: 1280px) {
	.social-box {
		position: relative;
		inset: 0;
		transform: rotate(0deg);
	}

	.social-icon {
		transform: rotate(0);
	}

	.languages-box {
		align-self: center;
	}
}

@media screen and (max-width: 600px) {
	header {
		padding: 20px 0 37px 0;

		.logo {
			max-width: 44px;
			min-height: 44px;
		}
	}

	.social-box {
		gap: 12px;

		span {
			&::after {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 480px) {
	.social-box {
		span {
			font-size: 16px;
		}
	}
}

@media screen and (max-width: 376px) {
	header {
		padding: 20px 0;
	}

	.social-box {
		span {
			display: none;
		}
	}
}